import { Route, Routes } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { lazy, useEffect } from 'react';
import { fetchCurrentUser } from 'redux/auth/fetchAuth';
// import { selectIsLoggedIn } from 'redux/auth/selectors';

import HomePage from 'pages/Home/HomePage';
import SharedLayout from './SharedLayout';
import PrivateRoute from './PrivatRoute/PrivatRoute';
import PublicRoute from './PublicRoute/PublicRoute';
import { selectIsRefreshing } from 'redux/auth/selectors';
import { BigPreLoader } from './Preloader/PreLoader';

const NotFoundPage = lazy(() => import('./NotFoundPage/NotFoundPage'));
const RegisterPage = lazy(() => import('pages/Register/RegisterPage'));
const LoginPage = lazy(() => import('pages/Login/LoginPage'));
const ContactsPage = lazy(() => import('pages/Contacts/ContactsPage'));

export const App = () => {
  // *       :
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchCurrentUser());
  }, [dispatch]);
  // */       :

  const isFetching = useSelector(selectIsRefreshing);
  // const isToken = useSelector(selectUserToken);
  // /         -   
  // /register -        
  // /login    -        
  // /contacts -        

  return isFetching ? (
    <BigPreLoader />
  ) : (
    <Routes>
      <Route path="/" element={<SharedLayout />}>
        {/* <Route path="/register" element={<RegisterPage />} /> */}
        {/* <Route path="/login" element={<LoginPage />} /> */}
        {/* <Route path="/contacts" element={<ContactsPage />} /> */}

        {/* <Route
            index
            element={<PublicRoute redirectTo="/" component={<HomePage />} />}
            /> */}

        <Route index element={<HomePage />} />

        <Route
          path="/register"
          element={
            <PublicRoute
              redirectTo="/contacts"
              component={<RegisterPage />}
              restricted
            />
          }
        />

        <Route
          path="/login"
          element={
            <PublicRoute
              redirectTo="/contacts"
              component={<LoginPage />}
              restricted
            />
          }
        />

        <Route
          path="/contacts"
          element={
            // ^  1
            <PrivateRoute redirectTo="/login" component={<ContactsPage />} />

            // ^  2
            // <PrivateRoute component={<ContactsPage />} />

            // ^  3
            // <PrivateRoute>
            //   <ContactsPage />
            // </PrivateRoute>
          }
        />
        <Route path="*" element={<NotFoundPage />}></Route>
      </Route>
    </Routes>
  );
};

