import axios from 'axios';
import { createAsyncThunk } from '@reduxjs/toolkit';

axios.defaults.baseURL = 'https://connections-api.herokuapp.com';

//    '        :
// Authorization: 'Bearer '
//          axios:
const tokenFn = {
  set(token) {
    // common -   -  
    // axios.defaults.headers.post.Authorization -   post-
    // axios.defaults.headers.get.Authorization -   get-
    axios.defaults.headers.common.Authorization = `Bearer ${token}`;
  },
  unset() {
    axios.defaults.headers.common.Authorization = '';
  },
};
//      fetch,        :
// fetch('url', {
//   method: 'POST',
//   header: {
//     Authorization: `Bearer ${token}`,
//   },
// });

export const fetchRegister = createAsyncThunk(
  'auth/register',
  async (credentials, { rejectWithValue }) => {
    // credentials - '  :
    // {
    //   "name": "string",
    //   "email": "string",
    //   "password": "string",
    // }
    //   ,  axios   stringify,     ',  axios    .
    try {
      //       
      const response = await axios.post('users/signup', credentials);
      //     pending/fulfilled/rejected

      // data :
      // {
      //   user: {
      //     name: "string",
      //     email: "string",
      //   },
      //   token: "string"
      // }

      // ^   -   tokenFn  :
      tokenFn.set(response.data.token); //  ,  axios    
      // axios.defaults.headers.common.Authorization = `Bearer ${response.data.token}`;

      return response.data;
    } catch (error) {
      return rejectWithValue(error.message);
    }
  }
);

export const fetchLogIn = createAsyncThunk(
  'auth/login',
  async (credentials, { rejectWithValue }) => {
    // credentials - '  :
    // {
    //   "email": "string",
    //   "password": "string",
    // }
    try {
      //       
      const response = await axios.post('/users/login', credentials);
      //     pending/fulfilled/rejected

      // ^   -   tokenFn  :
      tokenFn.set(response.data.token);
      // axios.defaults.headers.common.Authorization = `Bearer ${response.data.token}`;

      return response.data;
    } catch (error) {
      return rejectWithValue(error.message);
    }
  }
);

export const fetchLogOut = createAsyncThunk(
  'auth/logout',
  async (token, { rejectWithValue }) => {
    // credentials - '  :
    // {
    //   "email": "string",
    //   "password": "string",
    // }
    try {
      //       
      //     token -    ,      
      const response = await axios.post('/users/logout', token);
      //     pending/fulfilled/rejected

      // ^   -   tokenFn  :
      // axios.defaults.headers.common.Authorization = '';
      tokenFn.unset();

      return response.data;
    } catch (error) {
      return rejectWithValue(error.message);
    }
  }
);

// thunkAPI.getState() -   Redux- 
export const fetchCurrentUser = createAsyncThunk(
  'auth/refresh',
  async (_, { getState, rejectWithValue }) => {
    // credentials - '  :
    // {
    //   "email": "string",
    //   "password": "string",
    // }
    const state = getState();
    const receivedToken = state.storeAuth.token;

    if (receivedToken === null) {
      // retrun; -     -     undefined   .    .
      //       :
      return rejectWithValue(
        '  -  .   ,  '
      );
    }

    try {
      tokenFn.set(receivedToken);
      const response = await axios.get('/users/current');
      return response.data;
    } catch (error) {
      return rejectWithValue(error.message);
    }
  }
);

// USER
// POST   /users/signup    Create a new user
// POST   /users/login     Login user
// POST   /users/logout    Log out user

// User{
// id	string Backend-generated unique identifier.
// name*	string Username.
// email*	string E-mail address.
// password*	string Password.
// }
// example: OrderedMap { "name": "Adrian Cross", "email": "across@mail.com", "password": "examplepwd12345" }

// CONTACT
// GET     /contacts         Get all user contacts
