// ^   localStorage:
import { configureStore } from '@reduxjs/toolkit';

//   default-     :
import sliceFilterReducer from './phonebook/sliceFilter';
import sliceContactsReducer from './phonebook/sliceContacts';
import sliceAuthReducer from './auth/sliceAuth';

import {
  persistStore,
  persistReducer,
  // ~  ,      -    Redux-Persist
  FLUSH,
  REHYDRATE,
  PAUSE,
  PERSIST,
  PURGE,
  REGISTER,
} from 'redux-persist';

//        localStorage:
import storage from 'redux-persist/lib/storage';

// *           :
// // '  Redux-Persist  ( localStorage):
// const contactsPersistConfig = {
//   key: 'root', // ,   ,        (???)
//   storage, // storage: storage, -  storage  : import storage from 'redux-persist/lib/storage';
//   whitelist: ['stateContacts'], //       
// };

const authPersistConfig = {
  key: 'auth',
  storage,
  whitelist: ['token'], //           ' sliceAuth.initialState
};

// ~  middleware,      -    Redux-Persist
const middleware = getDefaultMiddleware =>
  getDefaultMiddleware({
    //    :
    serializableCheck: {
      //  ,   :
      ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
    },
  });

//   Redux    :
const storeRedux = configureStore({
  reducer: {
    // storeContacts: persistReducer(contactsPersistConfig, sliceContactsReducer),
    storeAuth: persistReducer(authPersistConfig, sliceAuthReducer),
    storeContacts: sliceContactsReducer,
    storeFilter: sliceFilterReducer,
  },

  // ~  middleware,      -    Redux-Persist
  middleware, // middleware: middleware,
});

// '  Redux-Persist    Redux :
export const persister = persistStore(storeRedux); //      <PersistGate>   index.js

export default storeRedux;
